<template>
  <div class="myrecord">
    <div class="searchArea">
      <input type="text" class="form-control col-xs-6" style="width:50%" v-model="search" placeholder="Host Team" v-on:keyup.enter="searchItem(search)">
      <button class="btn btn-default col-xs-6" v-on:click="hide">hide</button>
      <table class="table table-striped" v-show="target.length > 0">
        <thead>
          <tr>
            <th>Team</th>
            <th>State3</th>
            <th>State6</th>
            <th>S ALL</th>
            <th>Average</th>
          </tr>
        </thead>
        <tbody>
          <template v-for="item in target">
            <tr>
              <td>{{item.team}}</td>
              <td>{{item.l1 + '-' + item.r1}}</td>
              <td>{{item.l2 + '-' + item.r2}}</td>
              <td>{{item.l3 + '-' + item.r3}}</td>
              <td>{{((item.l1+item.l2+ item.l3)/3).toFixed(2) + '/' + ((item.r1+item.r2+item.r3)/3).toFixed(2)}}</td>
            </tr>
          </template>
        </tbody>
      </table>
    </div>
    <table class="table table-striped" v-show="!showLog">
      <thead>
        <tr>
          <th>Team</th>
          <th>State3</th>
          <th>State6</th>
          <th>S ALL</th>
          <th>Average</th>
        </tr>
      </thead>
      <tbody>
        <template v-for="item in myList">
          <tr>
            <td>{{item.team}}</td>
            <td>{{item.l1 + '-' + item.r1}}</td>
            <td>{{item.l2 + '-' + item.r2}}</td>
            <td>{{item.l3 + '-' + item.r3}}</td>
            <td>{{((parseFloat(item.l1)+parseFloat(item.l2)+ parseFloat(item.l3))/3).toFixed(2) + '/' + ((parseFloat(item.r1)+parseFloat(item.r2)+parseFloat(item.r3))/3).toFixed(2)}}</td>
          </tr>
        </template>
      </tbody>
    </table>
    <!-- <div class="button-area">
      <div class="btn-group btn-group-lg">
        <button type="button" class="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Log
          <span class="caret"></span>
        </button>
        <ul class="dropdown-menu">
          <li>
            <a v-on:click="toggleLog">{{showLog ? 'HideLog':'ShowLog'}}</a>
          </li>
          <li>
            <a v-show="showLog" v-on:click="saveLog">SaveLog</a>
          </li>
        </ul>
      </div>
      <div class="btn-group btn-group-lg">
        <button type="button" class="btn btn-default" v-on:click="save">Save</button>
      </div>
      <div class="btn-group btn-group-lg">
        <button type="button" class="btn btn-default" v-on:click="showProfits">Profits</button>
      </div>
    </div> -->
  </div>
</template>

<script>
import Store from '../services/store2.js';
export default {
  name: 'record6',
  data() {
    return {
      msg: 0,
      showLog: false,
      myList: Store.fetch().length > 0 ? Store.fetch() : [],
      editModel: [],
      search: '',
      target: []
    };
  },
  methods: {
    toggleLog: function() {
      this.showLog = !this.showLog;
      this.editModel = JSON.stringify(this.myList);
    },
    saveLog: function() {
      Store.save(JSON.parse(this.editModel));
    },
    searchItem: function(str) {
      this.target = this.myList.filter(v => {
        return v.team.indexOf(str) > -1;
      });
    },
    hide: function() {
      this.target = [];
      this.search = '';
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.button-area {
  position: fixed;
  width: 100%;
  bottom: 92px;
}
.myrecord {
  overflow-y: auto;
  padding-bottom: 92px;
}

table {
  word-break: break-all;
}
tr,
th,
tbody {
  text-align: center;
}

/*ul {
  list-style-type: none;
  max-height: 500px;
  padding: 0;
  overflow-y: auto;
}*/

/*li {
  position: relative;
  border-left: 10px solid red;
  border-top: 1px solid;
  border-bottom: 1px solid;
  height: 40px;
  line-height: 40px;
}*/

/*li:nth-child(2n+1) {
  border-left: 10px solid #7979c7;
}*/

a {
  color: #42b983;
}

.log {
  width: 80%;
  text-align: center;
  background-color: #c7edcc;
  margin: 10px auto;
  padding: 10px 20px;
  border-radius: 4px;
  border-radius: 4px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.5);
}

.center-btn {
  background: firebrick;
  width: 200px;
  height: 48px;
  border-radius: 4px;
  font-size: 1.6rem;
  margin: 10px;
}

.list-key {
  font-weight: bold;
}

li input {
  position: absolute;
  top: 0px;
  right: 0px;
  height: 30px;
  width: 40px;
  text-align: center;
  font-size: 1.2rem;
  border-radius: 50%;
  margin: 2px 1px;
}

span {
  font-size: 2.4rem;
  font-weight: bold;
}

.red {
  color: red;
}

.green {
  color: green;
}
.td-no-p {
  padding: 0;
  textarea {
    width: 100%;
    /* border-radius: 5px; */
    resize: none;
    color: #42b983;
  }
}
@media (max-height: 400px) {
  .button-area {
    display: none;
  }
}
</style>



